import * as React from 'react'
import { Card, Row, Col, Form, Input, Button, Table, DatePicker } from 'antd';
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import { getLotteryList } from '@/api/tenant'
import { asyncAwait } from '@/utils/util'

const formItemLayout = {
  labelCol: { span: 6 },
  wrapperCol: { span: 17 }
};
// const [form] = Form.useForm();

const tableColumns = [
  { title: '', dataIndex: 'wxnickname' },
  { title: '', dataIndex: 'phone' },
  { title: '', dataIndex: 'name' },
  { title: '', dataIndex: 'address4' },
  { title: '', dataIndex: 'address5' },
  { title: '', dataIndex: 'address6' },
  { title: '', dataIndex: 'address7' },
  { title: '', dataIndex: 'address8' },
  { title: '', dataIndex: 'address9' },
  { title: '', dataIndex: 'address10' }
]
export default class Lottery extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      expand: false,
      tableData: []
    }
    this.formRef = React.createRef()
  }

  onFinish = (values) => {
    console.log(values);
  }

  onHandleExpand = () => {
    this.setState({
      expand: !this.state.expand
    });
  }
  
  getData = async () => {
    const res = await asyncAwait(getLotteryList())
    console.info('res', res)
    if (res.code === '0') {
      this.setState({
        tableData: (res && res.data) || []
      })
    }
  }

  componentDidMount () {
    this.getData()
    console.info('formRef', this.formRef)
  }

  render () {
    const { tableData, expand } = this.state
    return (
      <div className="page-container">
        <Card className="module-card">
          <Form { ...formItemLayout } name="control-ref" onFinish={ this.onFinish } ref={ this.formRef }>
            <Row>
              <Col span={ 6 }>
                <Form.Item name="wxNickName" label="" rules={ [ { required: false } ] }>
                  <Input placeholder=""/>
                </Form.Item>
              </Col>
              <Col span={ 6 }>
                <Form.Item name="time" label="" rules={ [ { required: false } ] }>
                  <DatePicker.RangePicker showTime />
                </Form.Item>
              </Col>
              <Col span={ 6 }>
                <Form.Item name="phone" label="" rules={ [ { required: false } ] }>
                  <Input placeholder="" />
                </Form.Item>
              </Col>
              <Col span={ 6 } className="right-btns">
                <span onClick={ this.onHandleExpand }>
                  {expand ? <UpOutlined /> : <DownOutlined />}
                </span>
                <Button type="primary"></Button>
                <Button></Button>
              </Col>
            </Row>
            {
              expand ? 
              (<Row className="row-hide-item">
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
                <Col span={ 6 }>
                  <Form.Item name="note" label="" rules={ [ { required: false } ] }>
                    <Input />
                  </Form.Item>
                </Col>
              </Row>) : ''
            }
          </Form>
        </Card>

        <div className="table-container">
          <Table columns={ tableColumns } dataSource={ tableData } rowKey="id" />
        </div>
      </div>
    )
  }
}
